.gallery{display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));grid-template-rows:repeat(6, 200px);grid-row-gap:1rem;grid-auto-flow:dense}.gallery>figure:nth-child(4n){grid-column:span 2;grid-row:span 2}.gallery>figure:nth-child(8n){grid-column:span 3;grid-row:span 3}.gallery>figure{height:100%}.gallery>figure>img{width:100%;height:100%;object-fit:cover}


